@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
:root {
	--color-primary: #006289 !important;
	--color-secondary: #f3f4f6 !important;
	--color-tertiary: #dee2e6 !important;
	--color-light: #fff;
	--color-dark: #0f172a;
	--color-dark-theme: #111;
	--color-dark-primary: #222;
	--color-dark-secondary: #444;
	--color-border: #555;
	--color-danger: #ff0000;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: "Poppins", sans-serif;
	color: var(--color-dark);
}

.ask {
	&__wrapper {
		display: grid;
		grid-template-columns: 45% 1fr;
		grid-gap: 20px;
		@media screen and (max-width: "850px") {
			display: flex;
			flex-direction: column;
		}
	}
	&__tab {
		&__search {
			position: relative;
			margin-bottom: 15px;
			input {
				width: 100%;
				padding: 15px 15px !important;
				border: none;
				border: 1px solid var(--color-tertiary);
				font-size: 16px !important;
				height: auto !important;
				border-radius: 4px;
				font-family: "Poppins", sans-serif;
				&:focus {
					outline: none;
				}
			}
			span {
				position: absolute;
				top: 16px;
				right: 15px;
				cursor: pointer;
				color: var(--color-dark);
			}
		}
		&__questions {
			display: flex;
			flex-direction: column;
			border-radius: 4px;
			border: 1px solid var(--color-tertiary);
			overflow: auto;
			max-height: 340px;
			scrollbar-width: thin;
			&::-webkit-scrollbar {
				width: 0.5em;
				height: 0.5em;
			}
			&::-webkit-scrollbar-track {
				background-color: var(--color-tertiary);
				margin-block: 0.5em;
			}
			&::-webkit-scrollbar-thumb {
				background: var(--color-primary);
			}
			&::-webkit-scrollbar-thumb:hover {
				opacity: 0.2;
			}
			@media screen and (max-width: "850px") {
				max-height: 200px;
			}
		}
		&__question {
			background-color: inherit;
			float: left;
			border: none;
			outline: none;
			cursor: pointer;
			padding: 14px 16px;
			transition: 0.3s;
			position: relative;
			background-color: var(--color-light);
			&::before {
				position: absolute;
				content: "";
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				z-index: 10;
				width: 100%;
				height: 100%;
			}
			&:not(:last-child) {
				border-bottom: 1px solid var(--color-tertiary);
			}
			&:hover {
				background-color: var(--color-secondary);
			}
			&.ask-active {
				background-color: var(--color-tertiary);
			}
			&--text {
				margin: 0;
				color: var(--color-dark);
				font-size: 16px;
			}
		}
		&__content {
			display: none;
			height: 400px;
			&.ask-active {
				display: block;
			}
			@media screen and (max-width: "850px") {
				max-height: 400px;
				video {
					max-height: 400px;
				}
			}
			@media screen and (max-width: "650px") {
				max-height: 320px;
				height: auto;
			}
			video {
				width: 100%;
				height: 100%;
				border-radius: 5px;
				object-fit: cover;
			}
			p {
				padding: 15px;
				border-radius: 5px;
				border: 1px solid var(--color-tertiary);
				margin: 0;
				color: var(--color-dark);
				text-align: justify;
				height: 400px;
				overflow: auto;
				scrollbar-width: thin;
				background-color: var(--color-light);
				@media screen and (max-width: "850px") {
					height: 300px;
				}
				&::-webkit-scrollbar {
					width: 0.5em;
					height: 0.5em;
				}
				&::-webkit-scrollbar-track {
					background-color: var(--color-tertiary);
					margin-block: 0.5em;
				}
				&::-webkit-scrollbar-thumb {
					background: var(--color-primary);
				}
				&::-webkit-scrollbar-thumb:hover {
					opacity: 0.2;
				}
			}
		}
	}

	// AUDIO PLAYER CSS //
	&__audio {
		padding: 30px;
		padding-top: 40px;
		height: 400px;
		border-radius: 5px;
		background-color: var(--color-tertiary);
		display: flex;
		flex-direction: column;
		justify-content: center;
		@media screen and (max-width: "850px") {
			padding: 20px;
			padding-top: 30px;
			height: 300px;
		}
		.details {
			display: flex;
			align-items: center;
			flex-direction: column;
			justify-content: center;
		}
		.buttons {
			display: flex;
			justify-content: center;
			align-items: center;
		}
		.active {
			color: var(--color-dark);
		}
		.playpause-track {
			cursor: pointer;
			background-color: var(--color-primary);
			transition: all 0.5s;
			width: 80px;
			height: 80px;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			svg {
				fill: var(--color-light);
			}
			&:hover {
				background-color: var(--color-dark);
			}
		}
		.slider_container {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 10px;
		}
		.seek_slider,
		.volume_slider {
			appearance: none;
			height: 5px;
			background: var(--color-primary);
			transition: opacity 0.2s;
			cursor: pointer;
		}
		.seek_slider:hover,
		.volume_slider:hover {
			opacity: 1;
		}
		.seek_slider {
			width: 60%;
		}

		.volume_slider {
			width: 30%;
		}
		.seek_slider::-webkit-slider-thumb,
		.volume_slider::-webkit-slider-thumb {
			appearance: none;
			width: 15px;
			height: 15px;
			background: var(--color-light);
			border: 3px solid var(--color-dark);
			cursor: grab;
			border-radius: 100%;
		}

		.loader {
			height: 70px;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 25px;
			&.animatedPlay {
				.stroke {
					animation: inherit;
				}
			}
		}
		.loader .stroke {
			background: var(--color-light);
			height: 150%;
			width: 10px;
			border-radius: 50px;
			margin: 0 5px;
			animation: animate 1.4s linear infinite;
		}
		@keyframes animate {
			50% {
				height: 20%;
				background: var(--color-primary);
			}

			100% {
				background: var(--color-primary);
				height: 100%;
			}
		}
		.stroke:nth-child(1) {
			animation-delay: 0s;
		}
		.stroke:nth-child(2) {
			animation-delay: 0.3s;
		}
		.stroke:nth-child(3) {
			animation-delay: 0.6s;
		}
		.stroke:nth-child(4) {
			animation-delay: 0.9s;
		}
		.stroke:nth-child(5) {
			animation-delay: 0.6s;
		}
		.stroke:nth-child(6) {
			animation-delay: 0.3s;
		}
		.stroke:nth-child(7) {
			animation-delay: 0s;
		}
	}
	&__anything {
		margin-top: 20px;
		&__popup {
			display: none;
			position: relative;
			&.popup-show {
				display: block;
				.empty-content {
					position: fixed;
					top: 0;
					bottom: 0;
					right: 0;
					left: 0;
					z-index: 3000;
					background-color: rgba(0, 0, 0, 0.85);
					transition: all 0.5s;
				}
			}
		}
	}
	&__button__primary {
		background-color: var(--color-primary);
		padding: 10px 15px;
		color: var(--color-light);
		border-radius: 5px;
		text-decoration: none;
		border: none;
		cursor: pointer;
		&:focus {
			outline: none;
		}
	}
	&__title {
		text-align: center;
		margin-bottom: 40px;
		position: relative;
		padding-bottom: 5px;
		&::after {
			content: "";
			position: absolute;
			width: 150px;
			height: 4px;
			background-color: var(--color-primary);
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			border-radius: 2px;
		}
	}
	&__searchField {
		position: relative;
		max-width: 400px;
		margin: 0px auto 40px;
		display: flex;
		align-items: center;
		input {
			width: 100%;
			height: 44px;
			padding: 0 14px;
			border-radius: 4px;
			outline: none;
			font-family: "Poppins", sans-serif;
			border: 1px solid var(--color-tertiary);
		}
		span {
			width: 16px;
			height: 16px;
			top: 12px;
			position: absolute;
			right: 15px;
			color: var(--color-tertiary);
		}
	}

	//accordion style
	&__accordion {
		background-color: var(--color-light);
		color: var(--color-dark);
		cursor: pointer;
		padding: 15px;
		width: 100%;
		text-align: left;
		transition: 0.4s;
		display: flex;
		gap: 10px;
		align-items: center;
		justify-content: space-between;
		span {
			font-size: 18px;
			font-weight: 500;
		}
		&__wrapper {
			border: 1px solid var(--color-tertiary);
			border-radius: 4px;
			padding: 0 !important;
			margin: 0;
		}
		&__panel {
			padding: 0 18px;
			background-color: var(--color-light);
			max-height: 0;
			overflow: hidden;
			transition: max-height 0.2s ease-out;
			audio,
			p {
				padding: 10px 0;
			}
			video,
			iframe {
				object-fit: cover;
				padding: 10px 0;
				width: 100%;
				height: 500px;
				@media only screen and (max-width: "850px") {
					height: 400px;
				}
				@media only screen and (max-width: "650px") {
					height: 350px;
				}
				@media only screen and (max-width: "450px") {
					height: 250px;
				}
			}
		}
		&.ask__active .ask__closed__icon {
			display: none;
		}
		&.ask__active .ask__open__icon {
			display: inline-block;
		}
	}

	&__single__accordion {
		.title {
			color: var(--color-dark);
		}
		span {
			i {
				color: var(--color-dark);
			}
		}
		&:not(:last-child) {
			border-bottom: 1px solid var(--color-tertiary);
		}
	}

	&__active,
	&__accordion:hover {
		background-color: var(--color-secondary);
	}

	&__closed__icon {
		display: inline-block;
		font-size: 15px;
	}

	&__open__icon {
		display: none;
		font-size: 15px;
	}
	.ask-accordion {
		.ask__audio {
			background-color: var(--color-light);
			.loader .stroke {
				background-color: var(--color-tertiary);
			}
		}
	}
	&__anything__popup {
		display: none;
		position: relative;
		&.popup-show {
			display: block;
			.empty-content {
				position: fixed;
				top: 0;
				bottom: 0;
				right: 0;
				left: 0;
				z-index: 3000;
				background-color: rgba(0, 0, 0, 0.85);
				transition: all 0.5s;
			}
		}
	}
	.popup-content {
		position: fixed;
		margin: 10px;
		background: var(--color-light);
		z-index: 9999;
		text-align: left;
		border-radius: 5px;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 500px;
		max-height: 95%;
		font-size: 1em;
		&__head {
			p {
				font-size: 20px;
				margin: 0;
				padding: 15px;
				line-height: 1.3;
				cursor: inherit;
			}
			border-bottom: 1px solid var(--color-tertiary);
		}
		&__body {
			padding: 15px;
			font-size: 14px;
			&--title {
				margin-bottom: 15px;
				color: var(--color-dark);
				cursor: inherit;
			}
			form {
				input,
				textarea {
					width: 100%;
					padding: 10px;
					border-radius: 5px;
					border: 1px solid var(--color-tertiary);
					margin: 5px 0 15px;
					color: var(--color-dark);
					font-family: "Poppins", sans-serif;
					&:focus {
						outline: none;
					}
				}
				.result {
					padding: 15px;
					text-align: center;
					color: var(--color-primary);
				}
			}
			&--close {
				position: absolute;
				top: -12px;
				right: -12px;
				font-size: 15px;
				font-weight: bold;
				color: var(--color-light);
				cursor: pointer;
				background-color: var(--color-primary);
				width: 25px;
				height: 25px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
			}
		}
	}
	.no-question-found {
		text-align: center;
		padding: 10px 0;
		color: var(--color-danger);
		background-color: var(--color-light);
	}
}

.theme-dark {
	.ask {
		&__tab {
			&__search {
				input {
					background-color: var(--color-dark-secondary);
					color: var(--color-light);
					border-color: var(--color-border);
					&::placeholder {
						color: var(--color-light);
					}
					&:focus {
						background-color: var(--color-dark-secondary);
						color: var(--color-light);
						border-color: var(--color-border);
					}
				}
				span {
					color: var(--color-light);
				}
			}
			&__questions {
				border-color: var(--color-border);
			}
			&__question {
				border-color: var(--color-border);
				background-color: var(--color-dark-secondary);
				p {
					color: var(--color-light);
				}
				&:hover,
				&.ask-active {
					background-color: var(--color-dark-secondary);
				}
			}
			&__content {
				p {
					border-color: var(--color-border);
					color: var(--color-light);
					background-color: var(--color-dark-secondary);
				}
			}
		}
		&__audio {
			background-color: var(--color-dark-secondary);
			color: var(--color-light);
			.seek_slider::-webkit-slider-thumb,
			.volume_slider::-webkit-slider-thumb {
				background: var(--color-dark-secondary);
				border: 3px solid var(--color-light);
			}
			.playpause-track:hover {
				color: var(--color-dark-secondary);
			}
			.loader .stroke {
				background-color: var(--color-dark-secondary);
			}
		}
		&__title {
			color: var(--color-light);
		}
		&__searchField {
			input {
				background-color: var(--color-dark-secondary);
				color: var(--color-light);
				border-color: var(--color-border);
				&::placeholder {
					color: var(--color-light);
				}
				&:focus {
					background-color: var(--color-dark-secondary);
					color: var(--color-light);
					border-color: var(--color-border);
				}
			}
		}
		&__accordion {
			background-color: var(--color-dark-primary);
			// &__wrapper {
			//   border: 1px solid var(--color-border);
			// }
			&__panel {
				background-color: var(--color-dark-primary);
				color: var(--color-light);
			}
		}
		&__single__accordion {
			.title {
				color: var(--color-light);
			}
			span {
				i {
					color: var(--color-light);
				}
			}
			&:not(:last-child) {
				border-bottom: 1px solid var(--color-border);
			}
		}
		&__active,
		&__accordion:hover {
			background-color: var(--color-dark-secondary);
		}
		.ask-accordion {
			.ask__audio {
				background-color: var(--color-dark-primary-primary);
				.loader .stroke {
					background-color: var(--color-dark-secondary);
				}
			}
		}

		.popup-content {
			background-color: var(--color-dark-secondary);
			&__head {
				p {
					color: var(--color-light);
				}
				border-color: var(--color-border);
			}
			&__body {
				&--title {
					color: var(--color-light);
				}
				form {
					label {
						color: var(--color-light);
					}
					input,
					textarea {
						background-color: var(--color-dark-primary);
						border-color: var(--color-border);
						color: var(--color-light);
						&::placeholder {
							color: var(--color-light);
						}
					}
					.result {
						color: var(--color-primary);
					}
				}
			}
		}
		.no-question-found {
			background-color: var(--color-dark-secondary);
		}
		&.bg-one {
			background-color: var(--color-dark-secondary);
			.ask {
				&__tab {
					&__search {
						input {
							background-color: var(--color-dark-primary);
						}
					}
					&__question {
						background-color: var(--color-dark-primary);
						&:hover,
						&.ask-active {
							background-color: var(--color-dark-secondary);
						}
					}
					&__content {
						p {
							background-color: var(--color-dark-primary);
						}
					}
				}
				&__audio {
					background-color: var(--color-dark-primary);
				}

				&__searchField {
					input {
						background-color: var(--color-dark-primary);
					}
				}
				&__accordion {
					background-color: var(--color-dark-primary);
					&__panel {
						background-color: var(--color-dark-primary);
					}
				}
				&__active,
				&__accordion:hover {
					background-color: var(--color-dark-secondary);
				}
			}
			.no-question-found {
				background-color: var(--color-dark-primary);
			}
		}
	}
}
.ask__single__accordion.ask__active {
	.ask__accordion__panel.faq-answer {
		display: flex;
		align-items: center;
		gap: 10px;
		max-height: 150px;
		video {
			width: auto;
			height: auto;
		}
	}
	.block-editor-rich-text__editable.ask__accordion__panel.faq-answer.rich-text {
		gap: 0;
	}
}
